import React from 'react';
import styled from 'styled-components';
import Plus from '../../../assets/icons/Plus';
import Minus from '../../../assets/icons/Minus';
import Typography from '../Typography';

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  min-width: 100px;
`;

const Test = styled.div`
  width: 40px;
  height: 40px;
`;

const Value = styled.p`
  font-family: ${props => props.theme.fonts.mainFontFamily};
  font-size: 18px;
  text-align: center;
  padding: 0 16px;
`;

const ChangeValueButton = styled.button`
  background: transparent;
  border: none;
  padding: 0;
  width: 24px;
  height: 24px;
  outline: none;
  cursor:pointer;
`;

const QuantityPicker = ({ value, decrease, increase, maxValue,minValue=0, fill = '#fff' }) => {
  const handleDecrease = () => {
    if (value !== minValue) {
      decrease();
    }
  };
  const handleIncrease = () => {
    if (maxValue && value < maxValue){
      increase();
    }
    if(!maxValue){
      increase();
    }
  };
  return (
    <Wrapper>
      <ChangeValueButton
        onClick={handleDecrease}>
        <Minus
          fill={fill} />
      </ChangeValueButton>
      <Typography
        fontStyle='secondarySubtitle2'
        color='white'
        style={{paddingTop:'4px'}}
      >{value}</Typography>
      <ChangeValueButton
        onClick={handleIncrease}>
        <Plus
          fill={fill} />
      </ChangeValueButton>
    </Wrapper>
  );
};

export default QuantityPicker;