import axios from "axios";
import QueryString from "query-string";

const url = "https://core.spreedly.com/v1/payment_methods.json";

export default class SpreedlyApi {
  _url = "";
  _environmentKey = "";

  constructor(environmentKey) {
    this._environmentKey = environmentKey;
    this._url = QueryString.stringifyUrl({
      url,
      query: {
        environment_key: environmentKey,
      }
    });
  }

  async tokenize(card) {
    const payload = {
      payment_method: {
        credit_card: card,
      }
    };
    
    return axios.post(this._url, payload).then(data => data.data.transaction.payment_method.token);
  }
}