import React from 'react';
import ApplePayIcon from '../../assets/icons/CardIcons/ApplePayIcon';
import MastercardIcon from '../../assets/icons/CardIcons/MastercardIcon';
import VisaIcon from '../../assets/icons/CardIcons/VisaIcon';
import AmexIcon from '../../assets/icons/CardIcons/AmexIcon';
import {ReactComponent as DiscoverIcon} from '../../assets/icons/svg/cardIcons/discover-fill.svg';
import {ReactComponent as DefaultIcon} from '../../assets/icons/svg/cardIcons/card-default-fill.svg';
import {ReactComponent as StarIcon} from '../../assets/icons/svg/starIcon.svg';
import Typography from '../UI/Typography';
import {Wrapper,Content,CardWrap } from './styles';
const cardIcons= {
  AP:<ApplePayIcon/>,
  MC:<MastercardIcon/>,
  VI:<VisaIcon/>,
  AX:<AmexIcon/>,
  DS:<DiscoverIcon/>,
  default:<DefaultIcon/>,
};

const PaymentCardItem = ({ cardtype, last4Digits, selected,setToSelected,id}) => {
  return (
    <Wrapper onClick = {setToSelected}>
      <Content>
        <CardWrap>
          {cardIcons[cardtype] ?? cardIcons.default }
          <Typography
            fontStyle='secondarySubtitle4'
            color='dark50'
          > {cardtype ==='AP' ? 'Apple Pay' : `•••• ${last4Digits}`}</Typography>
        </CardWrap>
        <>
          {selected && <StarIcon/>}
        </>
      </Content>
    </Wrapper>
  );
};

export default PaymentCardItem;