import React, { useEffect, useReducer, useState } from 'react';
import { connect } from 'react-redux';
import images from 'react-payment-inputs/images';
import Nav from '../../components/Layout/Nav';
import { PageContent } from '../../globalStyles';
import AppFooter from '../../components/Layout/AppFooter';
import { Card } from '../../entities/payments';
import { libCardTypeToServerType } from '../../constants/payment';
import { getCards, createCard } from '../../store/wallet/actions';
import Typography from '../../components/UI/Typography';
import RoundIconButton from '../../components/UI/Buttons/RoundIconButton';
import { ReactComponent as LockIcon } from '../../assets/icons/svg/Lock.svg';
import { useHistory } from 'react-router-dom';

import {
  CardIcon,
  CardNumberInput,
  CardNumberInputWrap,
  FormRow,
  InputWrap,
  StyledInput,
  Wrapper,
} from './styles';
import { PaymentInputsContainer } from 'react-payment-inputs';
const Payments = ({ getCards, createCard, wallet }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [pan, setPan] = useState('');
  const [cvc, setCvc] = useState('');
  const [expiry, setExpiry] = useState('');
  const history = useHistory();
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
    getCards();
  }, []);

  const handleSave = (meta) => {
    const {
      cardType: { type },
    } = meta;

    if (!libCardTypeToServerType[type]) {
      alert('Cards of this type are not supported');
      return;
    }

    const [month, year] = expiry.split('/').map((i) => i.trim());
    const fullYear = `20${year}`;
    const card = new Card(
      'John',
      'Smith',
      pan,
      cvc,
      month,
      fullYear,
      libCardTypeToServerType[type],
    );
    createCard(card);
    history.push('/wallet');
  };

  return (
    <>
      <Nav showBackButton navBarTheme={navTheme} navBarLabel="Back"  />
      <PageContent background="dark">
        <Wrapper>
          <Typography
            fontStyle="primaryH3"
            color="secondaryDark"
            align="left"
            style={{ paddingBottom: '53px' }}
          >
            Securely add a Credit or Debit card
          </Typography>
          <PaymentInputsContainer>
            {({ meta, getCardImageProps, getCardNumberProps, getExpiryDateProps, getCVCProps }) => (
              <div>
                <>
                  <CardNumberInputWrap>
                    <label htmlFor="pan" style={{ display: 'flex' }}>
                      <Typography fontStyle="secondaryBody2" color="dark10">
                        Card number
                      </Typography>
                      <LockIcon style={{ marginLeft: '6px' }} />
                    </label>
                    <div style={{ position: 'relative' }}>
                      <CardIcon {...getCardImageProps({ images })} />
                      <CardNumberInput
                        id="pan"
                        {...getCardNumberProps({ onChange: (e) => setPan(e.target.value) })}
                        value={pan}
                      />
                    </div>
                  </CardNumberInputWrap>
                </>
                <FormRow>
                  <InputWrap>
                    <label htmlFor="expDate">
                      <Typography fontStyle="secondaryBody2" color="dark10">
                        Expiration
                      </Typography>
                    </label>

                    <StyledInput
                      id="expDate"
                      placeholder="0000 0000 0000 0000"
                      {...getExpiryDateProps({ onChange: (e) => setExpiry(e.target.value) })}
                      value={expiry}
                    />
                  </InputWrap>
                  <InputWrap>
                    <label htmlFor="cvc">
                      <Typography fontStyle="secondaryBody2" color="dark10">
                        CVC
                      </Typography>
                    </label>
                    <StyledInput
                      id="cvc"
                      {...getCVCProps({ onChange: (e) => setCvc(e.target.value) })}
                      value={cvc}
                    />
                  </InputWrap>
                  {!meta.error && <RoundIconButton  click={ () => handleSave(meta) } />}
                </FormRow>
                {meta.isTouched && meta.error && (
                  <Typography
                    fontStyle="secondaryBody2"
                    style={{ color: 'red', marginTop: '24px' }}
                  >
                    Error: {meta.error}
                  </Typography>
                )}
              </div>
            )}
          </PaymentInputsContainer>
        </Wrapper>
      </PageContent>
      <AppFooter footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = ({ wallet }) => {
  return {
    wallet,
  };
};

const mapDispatchToProps = {
  getCards,
  createCard,
};

export default connect(mapStateToProps, mapDispatchToProps)(Payments);
