import React, { useEffect, useReducer, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import { StyledPageContent, TopImage } from '../../globalStyles';
import { TopImageTextWrap, CheckBoxWrap, Content, PageFooter } from './styles';
import Typography from '../../components/UI/Typography';
import AppFooter from '../../components/Layout/AppFooter';
import Checkbox from '../../components/UI/Checkbox';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import { useHistory } from 'react-router-dom';
import Scroll from '../../components/Scroll';

const actionTypes = {
  setValue: 'setValue',
};
function preferencesReducer(state, [action, payload]) {
  let changedOptionIndex;
  switch (action) {
  case actionTypes.setValue:
    changedOptionIndex = state.findIndex( option => option.id === payload);
    return state.map( item => {
      if(item.id === payload){
        return {...item, selected:!item.selected};
      }
      return {...item};
    });
  default:
    return [...state];
  }
}

