import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Typography from '../../../../components/UI/Typography';
import Nav from '../../../../components/Layout/Nav';
import AppFooter from '../../../../components/Layout/AppFooter';
import PrimaryButtonWithIcon from '../../../../components/UI/Buttons/PrimaryButtonWithIcon';
import { Header, FormAndInfo, Info, Wrapper, NameInputWrapper, NameInput } from './styles';
import { PageContent } from '../../../../globalStyles';

const NameForm = ({ onSubmit, auth: { loading } }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [name, setName] = useState('');
  const inputRef = useRef();

  const handleEmailSubmit = (e) => {
    e.preventDefault();
    onSubmit(name);
  };

  const handleChangeEmail = (e) => {
    setName(e.target.value);
  };

  useEffect(() => {
    inputRef.current?.focus();
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);
  
  return (
    <>
      <Nav navBarTheme={navTheme} showBackButton navBarLabel="Rooms" prevDestination="/rooms" />
      <PageContent background="dark">
        <Wrapper>
          <Header>
            <Typography fontStyle="primaryH3" color="secondaryLight" align="left">
              Add your full name to sign up
            </Typography>
          </Header>
          <FormAndInfo>
            <NameInputWrapper onSubmit={handleEmailSubmit}>
              <NameInput ref={inputRef} value={name} onChange={handleChangeEmail} type="text" />
              <PrimaryButtonWithIcon disabled={name.length === 0 || loading}>
                Sign Up
              </PrimaryButtonWithIcon>
            </NameInputWrapper>

            <Info>
              <>
                <Typography
                  fontStyle="secondaryBody2"
                  color="dark10"
                  align="left"
                  style={{ marginBottom: '16px' }}
                >
                  By tapping sign up, you are agreeing to the
                </Typography>
                <Typography
                  fontStyle="secondarySubtitle3"
                  color="white"
                  align="left"
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    console.log('terms of use');
                  }}
                >
                  Selfbook terms of service and privacy policy.
                </Typography>
              </>
            </Info>
          </FormAndInfo>
        </Wrapper>
      </PageContent>
      <AppFooter footerTheme={navTheme} />
    </>
  );
};

NameForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
}; 

export default NameForm;
