import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import EmailForm from '../EmailForm';
import PhoneForm from '../PhoneForm';
import { type as contactType } from '../constants';

const typeToComponent = {
  [contactType.email]: EmailForm,
  [contactType.phone]: PhoneForm,
};

const getFormType = (user) => {
  return user.phone ? contactType.email : contactType.phone;
};

const ContactFormSwitcher = ({ user, ...rest}) => {
  const Component = typeToComponent[getFormType(user)];

  return (
    <>
      <Component
        {...rest}
      />
    </>
  );
};

ContactFormSwitcher.propTypes = {
  auth: PropTypes.object.isRequired,
};

export default ContactFormSwitcher;
