import React, { useRef } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import CarouselImage from './CarouselImage';

const CarouselWrapper = styled.div`
  position: relative;
  height: 144px;
  p {
     ${props => props.theme.fonts.caption1}
  }
  .slick-prev {
  }
  .slick-next {
  }
  .slick-arrow {
  }
  .slick-slide{
    opacity: .2;
    transition: opacity 1s;
  }
  .slick-active{
    opacity: 1;
    @media (max-width:768px){
       //padding-right: 200px !important;
  }
  }
  .slick-list{
  @media (max-width:768px){
       //padding: 0 !important;
  }
  }
  
`;

const SliderNavButton = styled.button.attrs({
  type: 'button',
})`
  position: absolute;
  top: 55px;
  z-index: 2;
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  outline: none;
  cursor: pointer;
`;

const PrevStyledButton = styled(SliderNavButton)`
  //left: 10px;
`;

const NextStyledButton = styled(SliderNavButton)`
  right: 0px;

`;

const Prev = ({ onClick }) => {
  return (
    <PrevStyledButton onClick={onClick}>
      <svg width="36" height="36" viewBox="0 0 36 36" fill="none" xmlns="http://www.w3.org/2000/svg">
        <g opacity="0.5">
          <rect opacity="0.01" width="36" height="36" transform="matrix(-1 0 0 1 36 0)" fill="white"/>
          <rect opacity="0.01" width="36" height="36" transform="matrix(-1 0 0 1 36 0)" fill="white"/>
          <rect width="18" height="1.5" transform="matrix(-0.707107 -0.707107 -0.707107 0.707107 24.8943 30.2779)" fill="black"/>
          <rect opacity="0.9" width="18" height="1.5" transform="matrix(-0.707107 0.707107 0.707107 0.707107 23.8337 5.85571)" fill="black"/>
        </g>
      </svg>
    </PrevStyledButton>
  );
};

const Next = ({ onClick }) => {
  return (
    <NextStyledButton onClick={onClick}>
      <svg width="36" height="36" viewBox="0 0 36 36" fill="none" xmlns="http://www.w3.org/2000/svg">
        <g opacity="0.5">
          <rect opacity="0.01" width="36" height="36" fill="white"/>
          <rect opacity="0.01" width="36" height="36" fill="white"/>
          <rect x="11.1057" y="30.2779" width="18" height="1.5" transform="rotate(-45 11.1057 30.2779)" fill="black"/>
          <rect opacity="0.9" x="12.1664" y="5.85571" width="18" height="1.5" transform="rotate(45 12.1664 5.85571)" fill="black"/>
        </g>
      </svg>
    </NextStyledButton>
  );
};

const Carousel = ({ description, data }) => {
  let settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    centerPadding: '45px',
    centerMode:true,
    slidesToScroll: 1,
    arrows: false,
  };

  const sliderRef = useRef(null);

  const toNext = () => {
    sliderRef.current.slickNext();
  };

  const toPrev = () => {
    sliderRef.current.slickPrev();
  };

  return (
    <CarouselWrapper>
      <Slider {...settings} ref={sliderRef}>
        {
          data.map((bg, index) => (
            <CarouselImage
              background={bg}
              key={index}
              label={description[index].headline}
              description={description[index].description}
            />
          ))
        }

      </Slider>
      <Prev onClick={toPrev}/>
      <Next onClick={toNext}/>
    </CarouselWrapper>
  );
};

Carousel.propTypes = {
  description: PropTypes.arrayOf(PropTypes.object).isRequired,
  data: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Carousel;


