import React, { useEffect, useReducer, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import { PageContent } from '../../globalStyles';
import AppFooter from '../../components/Layout/AppFooter';
import QuantityPicker from '../../components/UI/QuantityPicker';
import { connect } from 'react-redux';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import Typography from '../../components/UI/Typography';
import { useHistory } from 'react-router-dom';

import  { GuestType,ButtonWrapper,GuestsList,GuestTypeLabel,Wrapper} from './styles';
import { updateFilter } from '../../store/filter/actions';
const actionTypes = {
  incrementAdult: 'incrementAdult',
  decrementAdult: 'decrementAdult',
  incrementChildren: 'incrementChildren',
  decrementChildren: 'decrementChildren',
  addChildrenAge: 'addChildrenAge',
  incrementInfant: 'incrementInfant',
  decrementInfant: 'decrementInfant',
};

function guestsReducer(state, action) {
  switch (action) {
  case 'incrementAdult':
    return {
      ...state,
      adults: state.adults + 1,
    };
  case 'decrementAdult':
    return {
      ...state,
      adults: state.adults - 1,
    };
  case 'incrementChildren':
    return {
      ...state,
      childrenCount: state.childrenCount + 1,
      // childrenAges: state.childrenAges.concat([3]),
    };
  case 'decrementChildren':
    return {
      ...state,
      childrenCount: state.childrenCount - 1,
      // childrenAges: dropRight(state.childrenAges)
    };
  case 'incrementInfant':
    return {
      ...state,
      infants: state.infants + 1,
    };
  case 'decrementInfant':
    return {
      ...state,
      infants: state.infants - 1,
    };
  default:
    throw new Error();
  }
}

const AddGuests = ({ filter, updateFilter }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [guests, dispatch] = useReducer(guestsReducer, filter.guests);
  const history = useHistory();
  const handleUpdateGuests= () => {
    updateFilter({
      ...filter,
      guests
    });
    history.push('./calendar');
  };
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);
  return (
    <>
      <Nav
        showBackButton
        navBarTheme={navTheme}
        navBarLabel="Rooms"
        prevDestination="/room" />
      <PageContent
        background="dark">
        <Wrapper>
          <GuestsList>
            <GuestType>
              <div>
                <GuestTypeLabel
                  fontStyle="secondarySubtitle2"
                  color="white">
                  Adults
                </GuestTypeLabel>
                <Typography
                  fontStyle="secondaryBody2"
                  color="white">
                  Ages 18 or above
                </Typography>
              </div>
              <QuantityPicker
                value={guests.adults}
                decrease={() => dispatch(actionTypes.decrementAdult)}
                increase={() => dispatch(actionTypes.incrementAdult)}
              />
            </GuestType>
            <GuestType>
              <div>
                <GuestTypeLabel
                  fontStyle="secondarySubtitle2"
                  color="white">
                    Children
                </GuestTypeLabel>
                <Typography
                  fontStyle="secondaryBody2"
                  color="white">
                    Ages 2 to 17
                </Typography>
              </div>
              <QuantityPicker
                value={guests.childrenCount}
                decrease={() => {
                  return dispatch(actionTypes.decrementChildren);
                }}
                increase={() => dispatch(actionTypes.incrementChildren)}
              />
            </GuestType>
            <GuestType>
              <div>
                <GuestTypeLabel
                  fontStyle="secondarySubtitle2"
                  color="white">
                  Infants
                </GuestTypeLabel>
                <Typography
                  fontStyle="secondaryBody2"
                  color="white">
                  Under 2
                </Typography>
              </div>
              <QuantityPicker
                value={guests.infants}
                decrease={() => dispatch(actionTypes.decrementInfant)}
                increase={() => dispatch(actionTypes.incrementInfant)}
              />
            </GuestType>
          </GuestsList>
          <ButtonWrapper>
            <PrimaryButton
              onClick={handleUpdateGuests}
            >
              <Typography
                fontStyle="secondarySubtitle1"
                align="center"
                color="white">
                CONFIRM
              </Typography>
            </PrimaryButton>
          </ButtonWrapper>
          
        </Wrapper>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = (state) => {
  const { filter } = state;
  return {
    filter,
    // calendar,
  };
};
const mapDispatchToProps = {
  updateFilter
};
export default connect(mapStateToProps, mapDispatchToProps)(AddGuests);
