import React from 'react';
import IconButton from '../../UI/Buttons/IconButton';
import BackArrowIcon from '../../../assets/icons/BackArrowIcon';
import { Caption, CaptionSide, IconSide, NavWrap, StyledCloseIcon } from './style';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';

function Nav({ navBarTheme, showBackButton, navBarLabel, prevDestination }) {
  const history = useHistory();
  const handleGoBack = () => {
    if(prevDestination){    history.push(prevDestination);
    }
    else{
      history.goBack();
    }
  };
  const handleCloseWidget = () => {
    console.log('Close Widget');
  };
  return (
    <NavWrap>
      <CaptionSide
        customTheme={navBarTheme}>
        {showBackButton && (
          <IconButton
            onClick={handleGoBack}>
            <BackArrowIcon
              fill={navBarTheme} />
          </IconButton>
        )}
        <Caption
          addPaddingLeft={showBackButton}
          customTheme={navBarTheme}>
          {navBarLabel || 'Caption'}
        </Caption>
      </CaptionSide>
      <IconSide
        customTheme={navBarTheme}>
        <IconButton
          onClick={handleCloseWidget}>
          <StyledCloseIcon
            width="28px"
            height="32.67px"
            fill={navBarTheme}
          />
        </IconButton>
      </IconSide>
    </NavWrap>
  );
}

Nav.propTypes = {
  navBarTheme: PropTypes.oneOf(['white','black']),
  showBackButton: PropTypes.bool,
  navBarLabel: PropTypes.string,
  prevDestination: PropTypes.string,
};

export default Nav;
