import React from 'react';
import styled from 'styled-components';

const Input = styled.input`
  width: 100%;
  display: block;
  background: none;
  border: none;
  border-bottom: 1px solid #979797;
  color: white;
  padding: 11px 0;
  outline: none;
  margin-bottom: 15px;
  font-size: 17px;
  padding-bottom: 15px;
  font-family: ${props => props.theme.fonts.secondary.regular};
  &:disabled{
    background: hsla(0,0%,95%,1);
  }
  
`;
const Label = styled.label`
  text-align: ${props => props.align};
  color: hsla(0,0%,40%,1);
  font-family: ${props => props.theme.fonts.mainFontFamily};
  font-size: 14px;
  line-height: 16.41px;
  font-weight:200;
  margin:0 0 1px 0;
`;
const TextInput = ({disabled,placeholder,change,value,label, ...props}) => {
  return (
    <>
      <Label
        htmlFor={label}>{label}</Label>
      <Input
        type="text"
        name={label}
        placeholder={placeholder}
        value={value}
        onChange={change}
        disabled={disabled}
        {...props}
      />
    </>
  );
};

export default TextInput;