import React from 'react';
import Typography from '../Typography';
import styled from 'styled-components';
import ChevronRight_linear from '../../../assets/icons/ChevronRight_linear';
import { useHistory } from 'react-router-dom';

const ItemWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgb(102, 102, 102, 1);
  align-items: center;
  padding-bottom: 15px;
  margin-top: 23px;
  cursor: pointer;
  padding-top: 15px;
  &:hover {
    border-bottom: 1px solid white;
  }
  @media (max-width: 768px) {
    margin-top: 0px;
  }
  @media (max-width: 320px) {
    padding-bottom: 10px;
    padding-top: 10px;
  }
`;

const CalendarMenuItem = ({ label, destination }) => {
  const history = useHistory();
  const handleClick = () => {
    history.push(destination);
  };
  return (
    <ItemWrapper
      onClick={handleClick}>
      <Typography
        color="white"
        fontStyle="secondarySubtitle2">
        {label}
      </Typography>
      <ChevronRight_linear />
    </ItemWrapper>
  );
};

export default CalendarMenuItem;
