const Wallet = ({wallet,getCards}) => {
  const [navTheme, setNavTheme] = useState('white');
  const [selectedCard, setSelectedCard] = useState(wallet?.data.cards[0]?.id);
  useEffect(() => {
    getCards();
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('black');
  }, []);
  const history = useHistory();
  const handleAddCreditCardClick = () => {
    history.push('/payments');
  };
  return (
    <>
      <Nav
        showBackButton={true}
        navBarTheme={navTheme}
        navBarLabel="Back" />
      <StyledPageContent>
        <Header>
          
          <Typography
            fontStyle="primaryH1">Wallet</Typography>
          <AddCardButton onClick={handleAddCreditCardClick}>
            <DefaultCardIcon/>
            <Typography
              fontStyle="secondarySubtitle4">Add Credit or Debit card</Typography>
          </AddCardButton>
        </Header>
        <Scroll>
  
          <CardsWrapper>
            {wallet.data.cards.map((card) =>
              <PaymentCardItem
                key={card.id}
                id={card.id}
                last4Digits={card.last_4}
                cardtype={card.card_type}
                selected={selectedCard === card.id}
                setToSelected={ () => setSelectedCard(card.id)}
              />
            )}
          </CardsWrapper>
        </Scroll>

      </StyledPageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = ({ wallet }) => {
  return {
    wallet,
  };
};
const mapDispatchToProps = {
  getCards,
};
export default connect(mapStateToProps,mapDispatchToProps)(Wallet);
