import { toast } from "react-toastify";

export const error = (message) => {
  toast.error(message, {
    position: "top-center",
    autoClose: 5000,
    closeOnClick: true,
    draggable: true,
    progress: undefined,
  });
};

export const warn = (messages) => {
  toast.warn(messages.join(".\n"), {
    position: "top-center",
    autoClose: 5000,
    closeOnClick: true,
    draggable: true,
    progress: undefined,
  });
};