import { theme } from '../../../globalStyles';

export const fontResolver = ({ font }) => {
  switch (font) {
  case 'primary-regular': {
    return theme.fonts.primary.regular;
  }
  case 'primary-light': {
    return theme.fonts.primary.light;
  }
  case 'secondary-regular': {
    return theme.fonts.secondary.regular;
  }
  case 'secondary-bold': {
    return theme.fonts.secondary.bold;
  }
  default:
    return font || theme.fonts.secondary.regular;
  }
};
