export const componentResolver = (fontStyle) => {
  switch (fontStyle) {
  case 'primaryH1':
  case 'secondaryH1': {
    return 'h1';
  }
  case 'primaryH2':
  case 'secondaryH2': {
    return 'h2';
  }
  case 'primaryH3':
  case 'secondaryH3': {
    return 'h3';
  }
  case 'primaryH4': {
    return 'h4';
  }
  case 'primaryH5': {
    return 'h5';
  }
  case 'primaryH6': {
    return 'h6';
  }
  case 'primaryH7': {
    return 'h1';
  }
  case 'primaryH8':
  case 'primaryH9':
  case 'primaryH10':
  case 'primarySubtitle1':
  case 'primarySubtitle2':
  case 'primarySubtitle3':
  case 'primaryBody1':
  case 'primaryBody2':
  case 'primaryBody3':
  case 'secondarySubtitle1':
  case 'secondarySubtitle2':
  case 'secondarySubtitle3':
  case 'secondarySubtitle4':
  case 'secondaryBody1':
  case 'secondaryBody2':
  case 'secondaryBody3':
  case 'secondaryBody4':
  case 'secondaryCaption1':
  case 'secondaryCaption2':
  case 'secondaryCaption3': {
    return 'p';
  }
  case 'span': {
    return 'span';
  }
  default:
    return 'p';
  }
};
