import { forIn } from 'lodash';

export const generatePromoCodesString = (promocodes) => {
  let promoCodesToString='';
  
  //priority to display if many selected
  // iPreferred id, then promocode, , then coupon code, then group code, then special rates
  forIn(promocodes, (value,key) => {
    if( key==='membershipId' && value !== ''){
      promoCodesToString=`Membership ID: ${value}`;
    }
    else if(key==='agentId' && value !== ''){
      promoCodesToString=`Agent ID: ${value}`;
    }
    else if(key==='promoCode' && value !== ''){
      promoCodesToString=`Promo Code: ${value}`;
    }
    else if(key==='couponCode' && value !== ''){
      promoCodesToString=`Coupon Code: ${value}`;
    }
    else if(key==='groupCode' && value !== ''){
      promoCodesToString=`Group Code: ${value}`;
    }
    else if(key==='ada' && value ){
      promoCodesToString='ADA compliant only rooms only';
    }
    else if(key==='ratePlanCode' && value ){
      promoCodesToString=`Special Rate: ${value}`;
    }
  });
  return promoCodesToString;
};
