import React, { useEffect, useReducer, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import { PageContent } from '../../globalStyles';
import AppFooter from '../../components/Layout/AppFooter';
import AccordionItem from '../../components/UI/AccordionItem';
import TextInput from '../../components/UI/TextInput';
import { connect } from 'react-redux';
import Checkbox from '../../components/UI/Checkbox';
import PropTypes from 'prop-types';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import Typography from '../../components/UI/Typography';
import Scroll from '../../components/Scroll';
import { Accordion, CheckboxLabel, CheckboxLabelActive, CheckBoxWrap, Wrapper } from './styles';
import { updateFilter } from '../../store/filter/actions';
import { useHistory } from 'react-router-dom';

///// reducer
function promocodeReducer(state, [action, payload]) {
  switch (action) {
  case actionTypes.setPromoCode:
    return {
      ...state,
      promoCode: payload.toUpperCase(),
      groupCode: '',
      couponCode: '',
    };
  case actionTypes.setGroupCode:
    return {
      ...state,
      promoCode: '',
      groupCode: payload.toUpperCase(),
      couponCode: '',
    };
  case actionTypes.setCouponCode:
    return {
      ...state,
      promoCode: '',
      groupCode: '',
      couponCode: payload.toUpperCase(),
    };
  case actionTypes.setAgentId:
    return {
      ...state,
      agentId: payload.toUpperCase(),
    };
  case actionTypes.setMembershipId:
    return {
      ...state,
      membershipId: payload.toUpperCase(),
    };
  case actionTypes.setRatePlanCode:
    return {
      ...state,
      ratePlanCode: payload !== state.ratePlanCode ? payload : '',
    };
  case actionTypes.toggleADA:
    return {
      ...state,
      ada: !state.ada,
    };
  case actionTypes.setComments:
    return {
      ...state,
      comments: payload,
    };
  case actionTypes.setTravelPurpose:
    return {
      ...state,
      travelPurpose: payload !== state.travelPurpose ? payload : '',
    };
  default:
    throw new Error();
  }
}
const actionTypes = {
  setPromoCode: 'setPromoCode',
  setGroupCode: 'setGroupCode',
  setCouponCode: 'setCouponCode',
  setAgentId: 'setAgentId',
  setMembershipId: 'setMembershipId',
  setComments: 'setComments',
  toggleADA: 'toggleADA',
  setRatePlanCode: 'setRatePlanCode',
  setTravelPurpose: 'setTravelPurpose',
};

const Promocodes = ({ state, updateFilter }) => {
  const [promocodes, dispatchPromocodes] = useReducer(promocodeReducer, state.promocodes);
  const [navTheme, setNavTheme] = useState('white');
  const [activeItem, setActiveItem] = useState([true, false, false, false]);

  const history = useHistory();

  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);

  const handleShowContent = (id) => {
    setActiveItem(
      activeItem.map((item, index) => {
        if (id === index) {
          return !item;
        }
        return false;
      }),
    );
  };
  const handleUpdatePromocodes = () => {
    updateFilter({
      ...state,
      promocodes,
    });
    history.push('/calendar');
  };
  const handleInput = (e, actionType) => dispatchPromocodes([actionType, e.target.value]);

  return (
    <>
      <Nav
        showBackButton
        navBarTheme={navTheme}
        navBarLabel="Rooms"
        prevDestination="/room" />
      <PageContent
        background="dark">
        <Scroll>
          <Wrapper>
            <Accordion>
              <AccordionItem
                title="Special codes"
                index={0}
                showContent={activeItem[0]}
                clicked={handleShowContent}
              >
                <TextInput
                  placeholder="Promo code"
                  value={promocodes.promoCode}
                  disabled={false}
                  change={(e) => handleInput(e, actionTypes.setPromoCode)}
                />
                <TextInput
                  placeholder="Group code"
                  value={promocodes.groupCode}
                  change={(e) => handleInput(e, actionTypes.setGroupCode)}
                  disabled={false}
                />
                <TextInput
                  placeholder="Coupon code"
                  value={promocodes.couponCode}
                  change={(e) => handleInput(e, actionTypes.setCouponCode)}
                  disabled={false}
                />
                <TextInput
                  placeholder="Agent ID"
                  value={promocodes.agentId}
                  change={(e) => handleInput(e, actionTypes.setAgentId)}
                  disabled={false}
                />
                <TextInput
                  placeholder="Membership ID"
                  value={promocodes.membershipId}
                  change={(e) => handleInput(e, actionTypes.setMembershipId)}
                  disabled={false}
                />
              </AccordionItem>
              <AccordionItem
                title="Special rates"
                index={1}
                showContent={activeItem[1]}
                clicked={handleShowContent}
              >
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="bar"
                    fill="white"
                    selected={promocodes.ratePlanCode === 'BAR'}
                    handleChange={() => dispatchPromocodes([actionTypes.setRatePlanCode, 'BAR'])}
                  >
                    <CheckboxLabel>BAR</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="HOLLYWOOD"
                    fill="white"
                    selected={promocodes.ratePlanCode === 'HOLLYWOOD'}
                    handleChange={() =>
                      dispatchPromocodes([actionTypes.setRatePlanCode, 'HOLLYWOOD'])
                    }
                  >
                    <CheckboxLabel>HOLLYWOOD</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="yfil"
                    fill="white"
                    selected={promocodes.ratePlanCode === 'DBILL'}
                    handleChange={() => dispatchPromocodes([actionTypes.setRatePlanCode, 'DBILL'])}
                  >
                    <CheckboxLabel>DBILL</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="ada"
                    fill="white"
                    selected={promocodes.ada}
                    handleChange={() => dispatchPromocodes([actionTypes.toggleADA, ''])}
                  >
                    <CheckboxLabel>ADA compliant only rooms only</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
              </AccordionItem>
              <AccordionItem
                title="Special requests / Comment"
                index={2}
                showContent={activeItem[2]}
                clicked={handleShowContent}
              >
                <TextInput
                  placeholder="Enter Comments"
                  value={promocodes.comments}
                  disabled={false}
                  change={(e) => handleInput(e, actionTypes.setComments)}
                />
              </AccordionItem>
              <AccordionItem
                title="Purpose of Stay"
                index={3}
                showContent={activeItem[3]}
                clicked={handleShowContent}
              >
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="Business"
                    fill="white"
                    selected={promocodes.travelPurpose === 'Business'}
                    handleChange={() =>
                      dispatchPromocodes([actionTypes.setTravelPurpose, 'Business'])
                    }
                  >
                    <CheckboxLabel>Business</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
                <CheckBoxWrap>
                  <Checkbox
                    paddingLeft="0px"
                    label="leisure"
                    fill="white"
                    selected={promocodes.travelPurpose === 'Leisure'}
                    handleChange={() =>
                      dispatchPromocodes([actionTypes.setTravelPurpose, 'Leisure'])
                    }
                  >
                    <CheckboxLabel>Leisure</CheckboxLabel>
                  </Checkbox>
                </CheckBoxWrap>
              </AccordionItem>
            </Accordion>
            <PrimaryButton
              onClick={handleUpdatePromocodes}>
              <Typography
                fontStyle="secondarySubtitle1"
                align="center"
                color="white">
                Apply
              </Typography>
            </PrimaryButton>
          </Wrapper>
        </Scroll>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = (state) => {
  const { filter } = state;
  return {
    state: filter,
  };
};
const mapDispatchToProps = {
  updateFilter,
};

Promocodes.propTypes = {
  state: PropTypes.shape({
    promoCode: PropTypes.string,
    groupCode: PropTypes.string,
    couponCode: PropTypes.string,
    agentId: PropTypes.string,
    membershipId: PropTypes.string,
    bar: PropTypes.bool,
    yfil: PropTypes.bool,
    ada: PropTypes.bool,
    ratePlanCode: PropTypes.string,
    travelPurpose: PropTypes.string,
    comments: PropTypes.string,
  }),
};

export default connect(mapStateToProps, mapDispatchToProps)(Promocodes);
