import dayjs from 'dayjs';

export const getTimeSpanForDate = (service, date, timeStart = null) => {
  let timeSpan = null;
  
  if(timeStart) {
    timeSpan = service
      .dates.find(i => i.date_start === date)
      .time_spans.find(i => i.time_start === timeStart);
  } else {
    timeSpan = service.dates.find(i => i.date_start === date).time_spans[0];
  }

  return timeSpan;
};

export const getFirstTimeSpan = (service) => {
  return service.dates[0].time_spans[0];
};

export const getFirstPrice = (service) => {
  return getFirstTimeSpan(service).amount_before_tax;
};

export const getServiceTimeOptions = (service) => {
  return service.dates.map(date => ({
    label: dayjs(date.date_start).format('MMM, D YYYY'),
    value: date.date_start,
    children: date.time_spans
      .filter(timeSpan => timeSpan.time_start)
      .map(timeSpan => {
        const start = dayjs(`${date.date_start}${timeSpan.time_start}`);
        const format = 'HH:mm A';

        return {
          label: `${start.format(format)}`,
          value: timeSpan,
        };
      }),
  }));
};