import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import dayjs from 'dayjs';
import { initFilterDates } from '../../store/filter/actions';

function FilterInitializer({ children, filter, initFilterDates }) {
  useEffect(() => {
    (async () => {
      const start = dayjs(new Date()).add(10, 'day');
      const end = dayjs(new Date()).add(13, 'day');
      initFilterDates(start.format('YYYY-MM-DD'), end.format('YYYY-MM-DD'));
    })();
  }, []);

  const datesAreReady = filter.dateRange.start && filter.dateRange.end;

  return <>{datesAreReady && children}</>;
}

const mapStateToProps = (state) => {
  const { filter } = state;

  return {
    filter,
  };
};

const mapDispatchToProps = {
  initFilterDates,
};

export default connect(mapStateToProps, mapDispatchToProps)(FilterInitializer);
