import React, { useEffect, useState } from 'react';
import { PageContent } from '../../globalStyles';
import Nav from '../../components/Layout/Nav';
import AppFooter from '../../components/Layout/AppFooter';
import Carousel from './Carousel';
import PriceAndAddons from '../../components/PriceAndAddons';
import { RoomsData } from './RoomData';
import get from 'lodash/get';
import Scroll from '../../components/Scroll';
import Typography from '../../components/UI/Typography';
import SelectDate from '../../components/UI/SelectDate';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import PaymentMethod from '../../components/PaymentMethod';
import { DateInputWrap, PageFooter, PaymentMethodWrap, UpdateLink } from './style';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import dayjs from 'dayjs';

const getCarouselData = (rooms) =>
  rooms.map((room) => ({
    label: room.details.name,
    subtitle: room.details.short_description,
    background: get(room, 'details.images[0].url', ''),
    code: room.details.code,
  }));
const getDay = (date) => {
  return date.format('MMM D');
};

const getYear = (date) => {
  return date.format('YYYY');
};

const Room = ({ filter, date }) => {
  const [navTheme, setNavTheme] = useState('white');
  const history = useHistory();
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('black');
  }, []);
  const handleSelectDateClick = () => {
    history.push('/calendar');
  };
  const handleBookNow = () => {
    history.push('/login');
  };
  const room = RoomsData[0];
  return (
    <>
      <Nav
        showBackButton={false}
        navBarTheme={navTheme}
        navBarLabel="Only one room left" />
      <PageContent>
        <DateInputWrap>
          <SelectDate
            value={date}
            click={handleSelectDateClick}
          />
        </DateInputWrap>
        <Scroll>
          <Carousel
            data={getCarouselData(RoomsData)}
            afterChange={() => {}}
            onClick={() => {}} />
          <PriceAndAddons
            showModal={() => {}}
            services={room.services}
            selected={0}
            availability={room.availability}
            onService={() => {}}
          />
        </Scroll>
        <PageFooter>
          <PaymentMethodWrap>
            <PaymentMethod
              paymentMethod="applePay"
              variant="white" />
            <UpdateLink
              onClick={()=>history.push('/payments')}
              fontStyle="secondaryBody4"
              align="right"
              color="dark50">
              UPDATE
            </UpdateLink>
          </PaymentMethodWrap>

          <PrimaryButton
            onClick={handleBookNow}
          >
            <Typography
              fontStyle="secondarySubtitle1"
              align="center"
              color="white">
              Book Now
            </Typography>
          </PrimaryButton>
        </PageFooter>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = (state) => {
  const { filter, calendar } = state;
  const range = filter.dateRange;
  const start = dayjs(range.start);
  const end = dayjs(range.end);
  return {
    filter,
    date: `${getDay(start)} — ${getDay(end)}, ${getYear(end)} `
    // calendar,
  };
};
export default connect(mapStateToProps)(Room);
