import React, { useState } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import ContactSwitcherForCode from './parts/ContactSwitcherForCode';
import ConfirmationCodeForm from './parts/ConfirmationCodeForm';
import ContactFormSwitcher from './parts/ContactFormSwitcher';
import NameForm from './parts/NameForm';
import { getCode, login, updateUser } from '../../store/auth/actions';
import { userHasAllRequiredData } from '../../utils/auth';

const steps = [
  ContactSwitcherForCode,
  ConfirmationCodeForm,
  ContactFormSwitcher,
  NameForm,
];

const LoginFlow = ({ getCode, login, updateUser, auth, history, location }) => {
  const [step, setStep] = useState(0);
  const [concatForCode, setContactForCode] = useState({});
  const [userInfo, setUserInfo] = useState({});
  const from = get(location, 'state.from', { pathname: "/" });

  const Component = steps[step];
  const next = () => setStep(step + 1);
  const prev = () => setStep(step - 1);

  const sendCodeToContact = async (data) => {
    try {
      await getCode(data);
      setContactForCode(data);
      next();
    } catch(e) {
    }
  };

  const handleCodeSending = async (code) => {
    try {
      const res = await login({ ...concatForCode, code });
      if(userHasAllRequiredData(res.user)) {
        history.replace(from);
      } else {
        next();
      }
    } catch(e) {
    }
  };

  const handleAddContactInfo = (data) => {
    setUserInfo({ ...data });
    next();
  };

  const handleNameChange = async (name) => {
    try {
      const { id } = auth.data.user;
      await updateUser(id, { ...userInfo, full_name: name });
      history.replace(from);
    } catch(e) {
    }
  };

  const handlers = [
    sendCodeToContact,
    handleCodeSending,
    handleAddContactInfo,
    handleNameChange,
  ];

  return (
    <>
      <Component
        onSubmit={handlers[step]}
        resendCode={() => setStep(0)}
        user={userInfo}
        auth={auth}
      />
    </>
  );
};

LoginFlow.propTypes = {
  getCode: PropTypes.func.isRequired,
  login: PropTypes.func.isRequired,
}; 

const mapStateToProps = ({ auth }) => {
  return {
    auth,
  };
};

const mapDispatchToProps = {
  getCode,
  login,
  updateUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginFlow);
