import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Typography from '../../../../components/UI/Typography';
import Nav from '../../../../components/Layout/Nav';
import AppFooter from '../../../../components/Layout/AppFooter';
import PrimaryButtonWithIcon from '../../../../components/UI/Buttons/PrimaryButtonWithIcon';
import {
  Header,
  EmailInputWrapper,
  FormAndInfo,
  Info,
  Wrapper,
  EmailInput,
} from './style';
import { PageContent } from '../../../../globalStyles';
import { type as contactType } from '../constants';

const EmailFormForCode = ({ to, onSubmit, auth: { loading } }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [email, setEmail] = useState('');
  const inputRef = useRef();
  
  const handleEmailSubmit = (e) => {
    e.preventDefault();
    onSubmit({ email });
  };
  
  const handleChangeEmail = (e) => {
    setEmail(e.target.value);
  };

  useEffect(() => {
    inputRef.current?.focus();
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);

  return (
    <>
      <Nav
        navBarTheme={navTheme}
        showBackButton
        navBarLabel="Rooms"
        prevDestination="/rooms" />
      <PageContent
        background="dark">
        <Wrapper>
          <Header>
            <Typography
              fontStyle="primaryH3"
              color="secondaryLight"
              align="left"
            >
              Enter your email to sign up
            </Typography>
          </Header>
          <FormAndInfo>
            <EmailInputWrapper
              onSubmit={handleEmailSubmit}>
              <EmailInput
                ref={inputRef}
                value={email}
                onChange={handleChangeEmail}
                type="email"
              />
              <PrimaryButtonWithIcon
                disabled={!inputRef.current?.validity?.valid || loading}>
                Sign Up
              </PrimaryButtonWithIcon>
            </EmailInputWrapper>
            <Info>
              <Typography
                fontStyle="secondaryBody2"
                color="dark10"
                align="left"
                style={{ marginBottom: '16px' }}
              >
                We&apos;ll send you a verification code to this email address.
              </Typography>
              <Typography
                fontStyle="secondarySubtitle3"
                color="white"
                align="left"
                style={{ cursor: 'pointer' }}
                onClick={() => to(contactType.phone)}
              >
                Use your mobile number instead.
              </Typography>
            </Info>
          </FormAndInfo>
        </Wrapper>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

EmailFormForCode.propTypes = {
  to: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
}; 

export default EmailFormForCode;
