import React from 'react';
import { ThemeProvider } from 'styled-components';
import GlobalStyle, { theme } from './globalStyles';
import 'react-datepicker/dist/react-datepicker.css';
import { Switch, Route } from 'react-router-dom';
import PrivateRoute from './components/PrivateRoute';
import Layout from './components/Layout';

import Room from './screens/Room';
import Calendar from './screens/Calendar';
import FilterInitializer from './components/FilterInitializer';
import AddGuests from './screens/AddGuests';
import Promocodes from './screens/Promocodes';
import Wallet from './screens/Wallet';
import Payments from './screens/Payments';
import Confirmation from './screens/Confirmation';
import Addons from './screens/Addons';
import Itinerary from './screens/Itinerary';
import Login from './screens/LoginFlow';
import PriceDetails from './screens/PriceDetails';
import GuestProfile from './screens/GuestProfile';
import Preferences from './screens/Preferences';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Account from './screens/Account';
import Reservations from './screens/Reservations';

function App() {
  return (
    <>
      <GlobalStyle />
      <ToastContainer />
      <FilterInitializer>
        <ThemeProvider
          theme={theme}>
          <Layout>
            <Switch>
              <Route
                path="/login"
                component={Login} />
              <Route
                path="/calendar"
                component={Calendar} />
              <Route
                path="/addguests"
                component={AddGuests} />
              <Route
                path="/promocodes"
                component={Promocodes} />

              <PrivateRoute path="/wallet">
                <Wallet />
              </PrivateRoute>

              <PrivateRoute path="/payments">
                <Payments />
              </PrivateRoute>

              <Route
                path="/confirmation"
                component={Confirmation} />
              <Route
                path="/addons"
                component={Addons} />
              <Route
                path="/itinerary"
                component={Itinerary} />
              <Route
                path="/pricedetails"
                component={PriceDetails} />
              <Route
                path="/guestprofile"
                component={GuestProfile} />
              <Route
                path="/account"
                component={Account} />
              <Route
                path="/reservations"
                component={Reservations} />
              <Route
                path="/preferences"
                component={Preferences} />
              <Route
                path="*"
                component={Room} />
            </Switch>
          </Layout>
        </ThemeProvider>
      </FilterInitializer>
    </>
  );
}

export default App;
