import styled from 'styled-components';

const PrimaryButton = styled.button`
  text-transform: uppercase;
  width: 100%;
  height: 56px;
  border-radius: 100px;
  background-color: ${(props) =>
    props.variant === 'secondary' ? props.theme.colors.secondaryDark : props.theme.colors.dark100};
  color: white;
  border: none;
  outline: none;
  padding: 13px 0 11px 0;
  @media (max-width: 768px) {
    height: 44px;
    font-size: 14px;
  }
  &:hover {
    cursor: pointer;
    background-color: ${(props) =>
    props.variant === 'secondary'
      ? props.theme.colors.dark100
      : props.theme.colors.secondaryDark};
  }
  &:disabled {
    background-color: ${(props) => props.theme.colors.dark50};
    color: ${(props) => props.theme.colors.dark10};
  }
`;

export default PrimaryButton;
