import React, { useEffect, useState } from 'react';
import { StyledPageContent, TopImage } from '../../globalStyles';
import Nav from '../../components/Layout/Nav';
import AppFooter from '../../components/Layout/AppFooter';
import NemacolinLogo from '../../assets/icons/NemacolinLogo';
import { MenuItem, MenuItemWrapper, TopImageTextWrap } from './styles';
import Typography from '../../components/UI/Typography';
import StarIcon from '../../assets/icons/StarIcon';
import { useHistory } from 'react-router-dom';
import AccountIcon from '../../assets/icons/AccountIcon';
import WalletIcon from '../../assets/icons/WalletIcon';
import ReservationIcon from '../../assets/icons/ReservationIcon';
import SettingsIcon from '../../assets/icons/SettingsIcon';
import InviteIcon from '../../assets/icons/InviteIcon';
import Scroll  from '../../components/Scroll';

const GuestProfile = (props) => {
  const [navTheme, setNavTheme] = useState('white');
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('black');
  }, []);
  const history = useHistory();
  const menuItems = [
    {
      label:'Account',
      icon:<AccountIcon/>,
      destination:'/account'
    },
    {
      label:'Preferences',
      icon:<StarIcon/>,
      destination:'/preferences'
    },
    {
      label:'Wallet',
      icon:<WalletIcon/>,
      destination:'/wallet'
    },
    {
      label:'Reservations',
      icon:<ReservationIcon/>,
      destination:'/reservations'
    },
    {
      label:'Settings',
      icon:<SettingsIcon/>,
      destination:'/settings'
    },
    {
      label:'Invite a guest',
      icon:<InviteIcon/>,
      destination:'/inviteaguest'
    },
    
  ];
  const handleClickMenuItem = (destination) => {
    history.push(destination);
  };
  return (
    <>
      <Nav
        showBackButton={true}
        prevDestination='/confirmation'
        navBarTheme={navTheme}
        navBarLabel="Confirmation" />
      <StyledPageContent>
        <TopImage>
          <NemacolinLogo />
          <TopImageTextWrap>
            <Typography fontStyle="primaryH3" color="dark100" style={{paddingBottom:'16px'}}>
              Anthony
              King
            </Typography>
            <Typography
              fontStyle="primaryBody2"
              color="dark50">
              Guest since 2020
            </Typography>
          </TopImageTextWrap>
        </TopImage>
        <Scroll>
          {menuItems.map( item => (
            <MenuItemWrapper key={item.label}
            >
              <MenuItem
                onClick={() => handleClickMenuItem(item.destination)}
              >
                <Typography
                  fontStyle='secondarySubtitle4'
                  color='dark50'
                >
                  {item.label}
                </Typography>
                {item.icon}
              </MenuItem>
            </MenuItemWrapper>
          ))}
        </Scroll>
      </StyledPageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

export default GuestProfile;