import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import CarouselImage from './CarouselImage';
import { CarouselWrapper, NextStyledButton, PrevStyledButton } from './style';


const Prev = ({ onClick }) => {
  return (
    <PrevStyledButton
      onClick={onClick}>
      <svg
        width="36"
        height="36"
        viewBox="0 0 36 36"
        fill="none"
        xmlns="http://www.w3.org/2000/svg">
        <g
          opacity="0.5">
          <rect
            opacity="0.01"
            width="36"
            height="36"
            transform="matrix(-1 0 0 1 36 0)"
            fill="white"/>
          <rect
            opacity="0.01"
            width="36"
            height="36"
            transform="matrix(-1 0 0 1 36 0)"
            fill="white"/>
          <rect
            opacity="0.9"
            width="18"
            height="1.5"
            transform="matrix(-0.707107 -0.707107 -0.707107 0.707107 24.8943 30.2779)"
            fill="black"/>
          <rect
            opacity="0.9"
            width="18"
            height="1.5"
            transform="matrix(-0.707107 0.707107 0.707107 0.707107 23.8337 5.85571)"
            fill="black"/>
        </g>
      </svg>
    </PrevStyledButton>
  );
};

const Next = ({ onClick }) => {
  return (
    <NextStyledButton
      onClick={onClick}>
      <svg
        width="36"
        height="36"
        viewBox="0 0 36 36"
        fill="none"
        xmlns="http://www.w3.org/2000/svg">
        <g
          opacity="0.5">
          <rect
            opacity="0.01"
            width="36"
            height="36"
            fill="white"/>
          <rect
            opacity="0.01"
            width="36"
            height="36"
            fill="white"/>
          <rect
            opacity="0.9"
            x="11.1057"
            y="30.2779"
            width="18"
            height="1.5"
            transform="rotate(-45 11.1057 30.2779)"
            fill="black"/>
          <rect
            opacity="0.9"
            x="11.1057"
            y="5.85571"
            width="18"
            height="1.5"
            transform="rotate(45 12.1664 5.85571)"
            fill="black"/>
        </g>
      </svg>
    </NextStyledButton>
  );
};

const Carousel = ({ data, afterChange, onClick }) => {
  let settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    centerMode:true,
    centerPadding:'33px',
    slidesToScroll: 1,
    arrows: false,
    afterChange,
    draggable: false,
  };

  const sliderRef = useRef(null);

  const toNext = () => {
    sliderRef.current.slickNext();
  };

  const toPrev = () => {
    sliderRef.current.slickPrev();
  };
  const placeholderBg = 'https://www.italianbark.com/wp-content/uploads/2018/01/hotel-room-design-trends-italianbark-interior-design-blog.jpg';
  return (
    <CarouselWrapper>
      <Slider
        {...settings}
        ref={sliderRef}>
        {
          data.map((item, index) => (
            <CarouselImage
              key={index}
              background={placeholderBg}
              label={item.label}
              subtitle={item.subtitle}
              onClick={() => onClick(item.code)}
            />
          ))
        }

      </Slider>
      <Prev
        onClick={toPrev}/>
      <Next
        onClick={toNext}/>
    </CarouselWrapper>
  );
};

Carousel.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    subtitle: PropTypes.string,
    background: PropTypes.string,
  })).isRequired,
  onClick:PropTypes.func,
  afterChange:PropTypes.func,
};

export default Carousel;


