import React, { useEffect, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import { PageContent } from '../../globalStyles';
import AppFooter from '../../components/Layout/AppFooter';
import getDate from 'date-fns/getDate';
import subDays from 'date-fns/subDays';
import formatISO from 'date-fns/formatISO';
import DatePicker from 'react-datepicker';
import PropTypes from 'prop-types';
import 'react-datepicker/dist/react-datepicker.css';
import { useHistory } from 'react-router-dom';

import {
  CalendarDate,
  CalendarHeader,
  CalendarWrapper,
  ChangeMonthButton,
  MonthYear,
  Price,
  SoldOutCalendarDate,
  Wrapper,
  SoldOutDate,
  SoldOutPrice,
  MenuItemsWrapper,
  BottomWrapper,
  SoldoutOverlay,
} from './styles';
import { updateFilter } from '../../store/filter/actions';
import { connect } from 'react-redux';
import { calendarDummyData } from './DummyCalendarData';
import getYear from 'date-fns/getYear';
import getMonth from 'date-fns/getMonth';
import Typography from '../../components/UI/Typography';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import CalendarMenuItem from '../../components/UI/CalendarMenuItem';
import ChevronLeft_filled from '../../assets/icons/ChevronLeft_filled';
import ChevronRight_filled from '../../assets/icons/ChevronRight_filled';
import Scroll from '../../components/Scroll/Scroll';
import { generatePromoCodesString } from '../../utils/calendar';
import dayjs from 'dayjs';

const Calendar = ({ filter,updateFilter }) => {
  ////// all logic that uses calendar data from real API replaced with calendarDummyData object
  /////
  const calendar = calendarDummyData;
  ////
  const [navTheme, setNavTheme] = useState('white');
  const [startDate, setStartDate] = useState(new Date(filter.dateRange.start));
  const [endDate, setEndDate] = useState(new Date(filter.dateRange.end));
  const [currency, setCurrency] = useState(filter.currency);
  const [month, setMonth] = useState(new Date(filter.dateRange.start));
  const history = useHistory();
  const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  const guests = filter.guests;
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);

  const onChange = (dates) => {
    const [start, end] = dates;
    if (
      calendar.prices.find((item) => item?.date === formatDateToYYYYMMDD(start))?.price !== null
    ) {
      setStartDate(start);
    }
    setEndDate(end);
    //reset start date after remove selection
    if (startDate && endDate && !end) {
      setStartDate(null);
    }
  };

  const handleChangeMonth = (m) => {
    setMonth(m);
  };
  const handleUpdateCalendar = () => {
    updateFilter({
      ...filter,
      dateRange: {
        start: dayjs(startDate).format('YYYY-MM-DD'),
        end: dayjs(endDate).format('YYYY-MM-DD'),
      },
    });
    history.push('/room');
  };
  //////////TODO - move helpers to utils
  const formatDateToYYYYMMDD = (date) => formatISO(date, { representation: 'date' });
  const renderDayContents = (day, date) => {
    const selectedDay = calendar.prices.find((item) => formatDateToYYYYMMDD(date) === item.date);
    const price = selectedDay?.price
      ? displayedCurrency + selectedDay?.price.toString()
      : 'Sold Out';

    let returnDateCell = (
      <>
        <CalendarDate>{getDate(date)}</CalendarDate>
        <Price>{price}</Price>
      </>
    );

    if (
      selectedDay?.date &&
      new Date(selectedDay?.date) > subDays(new Date(), 1) &&
      price === 'Sold Out'
    ) {
      returnDateCell = (
        <SoldOutDate>
          <SoldOutCalendarDate>{getDate(date)}</SoldOutCalendarDate>
          <SoldOutPrice>{price}</SoldOutPrice>
        </SoldOutDate>
      );
    }

    if (
      endDate &&
      startDate &&
      formatDateToYYYYMMDD(endDate) !== formatDateToYYYYMMDD(startDate) &&
      formatDateToYYYYMMDD(endDate) === formatDateToYYYYMMDD(date)
    ) {
      returnDateCell = (
        <>
          <CalendarDate>{getDate(date)}</CalendarDate>
          <Price>Departure</Price>
        </>
      );
    }
    return returnDateCell;
  };
  const datesToExclude = () => {
    if (startDate) {
      const nearestUnavailableDate = calendar.prices.find(
        (item) => new Date(item?.date) > startDate && item?.price === null,
      );
      return (
        calendar.prices
          .filter(
            //filter dates larger then nearest unavailable
            (item) => new Date(item?.date) > new Date(nearestUnavailableDate?.date),
          )
          //return array of dates to exclude
          .map((item) => new Date(item?.date))
      );
    }
  };
  const displayedCurrency = currency === 'USD' ? '$' : '€';
  return (
    <>
      <Nav
        showBackButton
        navBarTheme={navTheme}
        navBarLabel="Rooms"
        prevDestination="/room" />
      <PageContent
        background="dark">
        <Scroll>
          <Wrapper>
            <CalendarWrapper>
              <DatePicker
                onMonthChange={handleChangeMonth}
                renderCustomHeader={({
                  date,
                  decreaseMonth,
                  increaseMonth,
                  prevMonthButtonDisabled,
                  nextMonthButtonDisabled,
                }) => (
                  <CalendarHeader>
                    <ChangeMonthButton
                      onClick={decreaseMonth}
                      disabled={prevMonthButtonDisabled}>
                      <ChevronLeft_filled />
                    </ChangeMonthButton>
                    <MonthYear
                      fontStyle="primaryH6"
                      color="secondaryDark">
                      {' '}
                      {months[getMonth(date)]} {getYear(date)}
                    </MonthYear>

                    <ChangeMonthButton
                      onClick={increaseMonth}
                      disabled={nextMonthButtonDisabled}>
                      <ChevronRight_filled />
                    </ChangeMonthButton>
                  </CalendarHeader>
                )}
                renderDayContents={renderDayContents}
                onChange={onChange}
                startDate={startDate}
                selected={startDate}
                endDate={endDate}
                onBlur={() => setStartDate(null)}
                minDate={new Date()}
                excludeDates={datesToExclude()}
                disabledKeyboardNavigation
                selectsRange
                inline
              />
            </CalendarWrapper>
            <BottomWrapper>
              <MenuItemsWrapper>
                <CalendarMenuItem
                  label={`${guests.adults} Adult${guests.adults > 1 ? 's' : ''}${
                    guests.childrenCount > 1 ? `, ${guests.childrenCount} Children` : ''
                  }${guests.childrenCount === 1 ? `, ${guests.childrenCount} Child` : ''}${
                    guests.infants > 1 ? `, ${guests.infants} Infants` : ''
                  }${guests.infants === 1 ? `, ${guests.infants} Infant` : ''}`}
                  destination="/addguests"
                />
                <CalendarMenuItem
                  label={generatePromoCodesString(filter.promocodes) ||  'Special codes'}
                  destination="/promocodes"
                />
              </MenuItemsWrapper>

              <PrimaryButton
                onClick={handleUpdateCalendar}
              >
                <Typography
                  fontStyle="secondarySubtitle1"
                  align="center"
                  color="white">
                  Continue
                </Typography>
              </PrimaryButton>
            </BottomWrapper>
          </Wrapper>
        </Scroll>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

const mapStateToProps = (state) => {
  const { filter, calendar } = state;

  return {
    filter,
    // calendar,
  };
};

const mapDispatchToProps = {
  updateFilter,
  // getRoomPrices,
};

Calendar.propTypes = {
  filter: PropTypes.shape({
    dateRange: PropTypes.shape({
      start: PropTypes.string.isRequired,
      end: PropTypes.string.isRequired,
    }).isRequired,
    priceRange: PropTypes.shape({
      start: PropTypes.number.isRequired,
      end: PropTypes.number.isRequired,
    }).isRequired,
    promocodes: PropTypes.shape({
      promoCode: PropTypes.string,
      groupCode: PropTypes.string,
      couponCode: PropTypes.string,
      agentId: PropTypes.string,
      membershipId: PropTypes.string,
      bar: PropTypes.bool,
      yfil: PropTypes.bool,
      ada: PropTypes.bool,
      ratePlanCode: PropTypes.string,
      travelPurpose: PropTypes.string,
      comments: PropTypes.string,
    }),
    guests: PropTypes.shape({
      adults: PropTypes.number.isRequired,
      childrenCount: PropTypes.number.isRequired,
      childrenAges: PropTypes.arrayOf(PropTypes.number).isRequired,
      infants: PropTypes.number.isRequired,
    }).isRequired,
  }).isRequired,
  calendar: PropTypes.shape({
    prices: PropTypes.arrayOf(
      PropTypes.shape({
        date: PropTypes.string.isRequired,
        price: PropTypes.string.isRequired,
      }),
    ).isRequired,
  }),
  updateFilter: PropTypes.func.isRequired,
  getRoomPrices: PropTypes.func.isRequired,
  onContinue: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Calendar);
