import keyBy from 'lodash/keyBy';
import { INIT_FILTER_DATES, UPDATE_FILTER } from './actions';
import { success, error } from '../../utils/store';

const promocodesInitialState = {
  promoCode: '',
  groupCode: '',
  couponCode: '',
  agentId: '',
  membershipId: '',
  ratePlanCode: '',
  // bar:false,
  // DBILL:false,
  // HOLLYWOOD:false,
  ada: false,
  travelPurpose: '',
  comments: '',
};

const guestsInitialState = {
  adults: 2,
  childrenCount: 0,
  childrenAges: [],
  infants: 0,
};

const initialState = {
  dateRange: {
    start: '',
    end: '',
  },
  priceRange: {
    start: 0,
    end: 1000,
  },
  promocodes: promocodesInitialState,
  guests: guestsInitialState,
  currency: 'USD',
};

export default (state = initialState, action) => {
  const { payload } = action;

  switch (action.type) {
  case INIT_FILTER_DATES: {
    const { start, end } = payload;
    return {
      ...state,
      dateRange: {
        start,
        end,
      },
    };
  }
  case UPDATE_FILTER: {
    return {
      ...state,
      ...payload,
    };
  }
  default:
    return state;
  }
};
