import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import EmailFormForCode from '../EmailFormForCode';
import PhoneFormForCode from '../PhoneFormForCode';
import { type as contactType } from '../constants';

const typeToComponent = {
  [contactType.email]: EmailFormForCode,
  [contactType.phone]: PhoneFormForCode,
};

const ContactSwitcherForCode = (props) => {
  const [type, setType] = useState(contactType.email);
  const Component = typeToComponent[type];

  return (
    <>
      <Component
        {...props}
        to={setType}
      />
    </>
  );
};

ContactSwitcherForCode.propTypes = {
};

export default ContactSwitcherForCode;
