import React, { useEffect, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import { StyledPageContent, TopImage } from '../../globalStyles';
import AppFooter from '../../components/Layout/AppFooter';
import { useHistory } from 'react-router-dom';

import {
  Content,
  Reservation,
  ReservationHeader,
  Tab,
  TabWrapper,
  TopImageTextWrap,
} from './styles';
import Typography from '../../components/UI/Typography';
import { TextButtonIconRight } from '../../components/UI/Buttons/TextButtonIconRight/TextButtonIconRight';
import ArrowRight from '../../assets/icons/RightArrowIcon';
import Scroll from '../../components/Scroll';
const reservationDummyData=[{
  id:'1',
  hotel:'Chateau',
  room: 'Chateau King\n',
  dates:'NOV 06 — NOV 09, 2020',
  status:'current'
},
{
  id:'2',
  hotel:'Chateau',
  room: 'Chateau King\n',
  dates:'NOV 06 — NOV 09, 2020',
  status:'past'
},
{
  id:'3',
  hotel:'Chateau',
  room: 'Chateau King\n',
  dates:'NOV 06 — NOV 09, 2020',
  status:'upcoming'
},
{
  id:'4',
  hotel:'Chateau',
  room: 'Chateau King\n',
  dates:'NOV 06 — NOV 09, 2020',
  status:'upcoming'
}
];
const Reservations = (props) => {
  const [navTheme, setNavTheme] = useState('white');
  const [activeTab, setActiveTab] = useState('current');
  const history = useHistory();
  
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('black');
  }, []);
  const tabs = ['current','upcoming','past'];
  return (
    <>
      <Nav
        showBackButton={true}
        prevDestination="/guestprofile"
        navBarTheme={navTheme}
        navBarLabel="Profile"
      />
      <StyledPageContent style={{ background: 'white' }}>
        <TopImage>
          <TopImageTextWrap>
            <Typography fontStyle="primaryH3" color="dark100">
              Reservations
            </Typography>
            <TabWrapper>
              {tabs.map(tab => (
                <Tab key={tab}
                  onClick={()=>setActiveTab(tab)}
                >
                  <Typography
                    fontStyle='secondaryBody3'
                    color={activeTab === tab ?'dark100' : 'dark50'}
                    style={{ textDecoration: activeTab === tab ? 'underline': 'none' }}>
                    {tab.toUpperCase()}
                  </Typography>
                </Tab>
              ))}
            </TabWrapper>
          </TopImageTextWrap>
        </TopImage>
        <Scroll>
          <Content>
            {reservationDummyData.filter(item => item.status === activeTab).map(item => (
              <Reservation key={item.id}>
                <ReservationHeader>
                  <Typography
                    fontStyle='secondarySubtitle3'
                    color='dark50'
                  >
                    {item.hotel}
                  </Typography>
                  <TextButtonIconRight onClick={()=>history.push('/itinerary')}>
                    <Typography fontStyle="secondaryBody3" style={{ paddingRight: '12px' }}>
                    RECEIPT
                    </Typography>
                    <ArrowRight />
                  </TextButtonIconRight>
                </ReservationHeader>
                <Typography
                  fontStyle='secondarySubtitle4'
                  color='dark50'
                  style={{paddingBottom:'4px'}}
                >
                  {item.room}
                </Typography>
                <Typography
                  fontStyle='secondarySubtitle4'
                  color='dark50'
                >
                  {item.dates}
                </Typography>
              </Reservation>
            ))}
          </Content>
        </Scroll>
      </StyledPageContent>
      <AppFooter footerTheme={navTheme} />

    </>
  );
};

export default Reservations;