import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import {
  Header,
  FormAndInfo,
  Info,
  Wrapper,
  NumberInput,
} from './styles';
import Nav from '../../../../components/Layout/Nav';
import { PageContent } from '../../../../globalStyles';
import Typography from '../../../../components/UI/Typography';
import AppFooter from '../../../../components/Layout/AppFooter';
import PhoneInputForm from '../../../../components/PhoneInputForm';

const PhoneForm = ({ onSubmit, auth: { loading } }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [phone, setPhone] = useState('+1');
  const inputRef = useRef();
  
  const handleSubmitPhoneNumber = (e) => {
    e.preventDefault();
    onSubmit({ phone });
  };
  
  const handleChangePhoneNumber = (e) => {
    setPhone(e.target.value);
  };
  
  useEffect(() => {
    inputRef.current?.focus();
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);
  
  return (
    <>
      <Nav
        navBarTheme={navTheme}
        showBackButton
        navBarLabel="Rooms"
        prevDestination="/rooms" />
      <PageContent
        background="dark">
        <Wrapper>
          <Header>
            <Typography
              fontStyle="primaryH3"
              color="secondaryLight"
              align="left">
              Enter your mobile number
            </Typography>
          </Header>
          <FormAndInfo>
            <NumberInput>
              <PhoneInputForm
                ref={inputRef}
                submitForm={handleSubmitPhoneNumber}
                phone={phone}
                changePhone={handleChangePhoneNumber}
                showSubmit={phone.length === 12 && !loading}
              />
            </NumberInput>
            <Info>
              <>
                <Typography
                  fontStyle="secondaryBody2"
                  color="dark10"
                  align="left"
                  style={{ marginBottom: '16px' }}
                >
                  By tapping sign up, you are agreeing to the
                </Typography>
                <Typography
                  fontStyle="secondarySubtitle3"
                  color="white"
                  align="left"
                  style={{ cursor: 'pointer' }}
                  onClick={() => { }}
                >
                  Selfbook terms of service and privacy policy.
                </Typography>
              </>
            </Info>
          </FormAndInfo>
        </Wrapper>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

PhoneForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
};

export default PhoneForm;