import { GET_CARDS, CREATE_CARD } from './actions';
import { success, error } from '../../utils/store';

const initialState = {
  data: {
    cards: [],
  },
  loading: false,
  error: null,
};

export default (state = initialState, action) => {
  const { payload } = action;

  switch (action.type) {
    case CREATE_CARD:
    case GET_CARDS: {
      return {
        ...state,
        loading: true,
        error: null,
      };
    }
    case success(GET_CARDS): {
      return {
        ...state,
        data: {
          ...state.data,
          cards: payload,
        },
      };
    }
    case success(CREATE_CARD): {
      return {
        ...state,
        data: {
          ...state.data,
          cards: [payload, ...state.data.cards],
        },
      };
    }
    case error(CREATE_CARD):
    case error(GET_CARDS): {
      return {
        ...state,
        error: payload,
      };
    }
    default:
      return state;
  }
};
