import React from 'react';
import styled from 'styled-components';
import { componentResolver, styleResolver } from './resolvers';
import PropTypes from 'prop-types';

const Component = styled.p`
  ${(props) => styleResolver(false, props.fontStyle)}
  text-align:${(props) => props.align};
  color: ${(props) => props.theme.colors[props.color]};
  @media (max-width: 768px) {
    ${(props) => styleResolver(true, props.fontStyle)}
  }
`;
const Typography = ({ fontStyle, color, ...other }) => {
  const component = componentResolver(fontStyle);
  return <Component
    color={color}
    as={component}
    fontStyle={fontStyle}
    {...other} />;
};

Typography.propTypes = {
  /**
   * Set the text-align on the component.
   */
  align: PropTypes.oneOf(['inherit', 'left', 'center', 'right', 'justify']),
  /**
   * The content of the component.
   */
  // children: PropTypes.node,
  /**
   * Set the color of the component.
   */
  color: PropTypes.oneOf([
    'primaryDark',
    'primaryLight',
    'secondaryDark',
    'secondaryLight',
    'dark100',
    'dark50',
    'dark10',
    'white'
  ]),
  /**
   * Applies the theme typography styles & rendered tag
   */
  fontStyle: PropTypes.oneOf([
    'primaryH1',
    'primaryH2',
    'primaryH3',
    'primaryH4',
    'primaryH5',
    'primaryH6',
    'primaryH7',
    'primaryH8',
    'primaryH9',
    'primaryH10',
    'primarySubtitle1',
    'primarySubtitle2',
    'primarySubtitle3',
    'primaryBody1',
    'primaryBody2',
    'primaryBody3',
    'secondaryH1',
    'secondaryH2',
    'secondaryH3',
    'secondarySubtitle1',
    'secondarySubtitle2',
    'secondarySubtitle3',
    'secondarySubtitle4',
    'secondaryBody1',
    'secondaryBody2',
    'secondaryBody3',
    'secondaryBody4',
    'secondaryCaption1',
    'secondaryCaption2',
    'secondaryCaption3',
    'span',
  ]),
};
export default Typography;
