import React from 'react';
import styled from 'styled-components';
import Typography from '../../Typography';
import RightArrowIcon from '../../../../assets/icons/RightArrowIcon';
import PropTypes from 'prop-types';
const Button = styled.button`
  background: ${props => props.theme.colors.white};
  height: 56px;
  color:${props => props.theme.colors.dark100} ;
  border-radius: 100px;
  display: flex;
  justify-content: space-between;
  border: none;
  align-items: center;
  text-transform: uppercase;
  padding: 0 20px;
  outline: none;
  cursor: pointer;
  width: 100%;
  &:hover{
    background-color: ${props => props.theme.colors.dark10};
  }
  &:disabled{
      background: ${props => props.theme.colors.primaryLight};
      cursor: not-allowed;
  }
`;
const PrimaryButtonWithIcon = ({children, disabled}) => {
  return (
    <Button
      disabled={disabled}>
      <Typography
        fontStyle='secondarySubtitle1'
        color={disabled ? 'white' : 'dark100'}>{children}</Typography>
      <RightArrowIcon
        fill={disabled ? 'white': 'black'}/>
    </Button>
  );
};

export default PrimaryButtonWithIcon;

PrimaryButtonWithIcon.propTypes = {
  children:PropTypes.node,
  disabled:PropTypes.bool,
};
