import { success, error } from '../../utils/store';
import AuthApi from '../../services/auth/api';
import { getErrorMessage } from '../../utils/api';
import { setToken, removeToken } from "../../utils/axios";
import axios from "../../configs/axios";

export const GET_CODE = 'GET_CODE';

const getCodeRequest = () => {
  return {
    type: GET_CODE,
  };
};

const getCodeSuccess = (payload) => {
  return {
    type: success(GET_CODE),
    payload,
  };
};

const getCodeError = (msg) => {
  return {
    type: error(GET_CODE),
    payload: msg,
  };
};

export const getCode = (data) => async (dispatch) => {
  try {
    dispatch(getCodeRequest());
    await new AuthApi().getCode(data);
    dispatch(getCodeSuccess());
  } catch (e) {
    const msg = getErrorMessage(e);
    dispatch(getCodeError(msg));
    throw new Error(msg);
  }
};

export const getCodeViaEmail = (email) => async (dispatch) => {
  await dispatch(getCode({ email }));
};

export const getCodeViaPhone = (phone) => async (dispatch) => {
  await dispatch(getCode({ phone }));
};

export const LOGIN = 'LOGIN';

const loginRequest = () => {
  return {
    type: LOGIN,
  };
};

const loginSuccess = (payload) => {
  return {
    type: success(LOGIN),
    payload,
  };
};

const loginError = (msg) => {
  return {
    type: error(LOGIN),
    payload: msg,
  };
};

export const login = (payload) => async (dispatch) => {
  let data = null;

  try {
    dispatch(loginRequest());
    data = await new AuthApi().login(payload);
    setToken(axios, data.token);
    dispatch(loginSuccess(data));
  } catch (e) {
    const msg = getErrorMessage(e);
    dispatch(loginError(msg));
    throw new Error(msg);
  }

  return data;
};

export const loginViaEmail = (code, email) => async (dispatch) => {
  await dispatch(login({ code, email }));
};

export const loginViaPhone = (code, phone) => async (dispatch) => {
  await dispatch(login({ code, phone }));
};

export const UPDATE_USER = 'UPDATE_USER';

const updateUserRequest = () => {
  return {
    type: UPDATE_USER,
  };
};

const updateUserSuccess = (payload) => {
  return {
    type: success(UPDATE_USER),
    payload,
  };
};

const updateUserError = (msg) => {
  return {
    type: error(UPDATE_USER),
    payload: msg,
  };
};

export const updateUser = (id, payload) => async (dispatch) => {
  try {
    dispatch(updateUserRequest());
    const user = await new AuthApi().updateUser(id, payload);
    dispatch(updateUserSuccess(user));
  } catch (e) {
    const msg = getErrorMessage(e);
    dispatch(updateUserError(msg));
    throw new Error(msg);
  }
};

export const LOGOUT = 'LOGOUT';

export const logout = () => async (dispatch) => {
  removeToken(axios);
  dispatch({
    type: LOGOUT,
  });
};

export const RESTORE_TOKEN = 'RESTORE_TOKEN';
