import React from 'react';
import styled from 'styled-components';
import ChevronBottom from '../../../assets/icons/ChevronRight_linear';

import ChevronTop from '../../../assets/icons/ChevronTop_linear';
import Typography from '../Typography';

const AccordionItemWrap = styled.div`
  margin-bottom: 12px;
  //&:last-of-type{margin-top: 24px;}
`;
const AccordionHeader = styled.div`
  display: flex;
  border-bottom: 1px solid hsla(0, 0%, 67%, 1);
  padding-bottom: 10px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
  height: 56px;
`;
const AccordionTitle = styled(Typography)`
  margin: 0;
  //color: ;
  @media (max-width: 768px) {
    font-size: 16px;
  }
`;
const AccordionItem = ({ title, children, showContent, clicked, index }) => {
  return (
    <AccordionItemWrap>
      <AccordionHeader
        onClick={() => clicked(index)}>
        <AccordionTitle
          fontStyle="secondaryH2"
          color="white">
          {title}
        </AccordionTitle>
        {showContent ? <ChevronTop
          size="24" /> : <ChevronBottom
          fill="white" />}
      </AccordionHeader>
      {showContent && children}
    </AccordionItemWrap>
  );
};

export default AccordionItem;
