import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Typography from '../../../../components/UI/Typography';
import Nav from '../../../../components/Layout/Nav';
import AppFooter from '../../../../components/Layout/AppFooter';
import ReactCodeInput from 'react-verification-code-input';
import { Header, Info, VerificationCodeInput, Wrapper } from './styles';
import { PageContent } from '../../../../globalStyles';

const ConfirmationCodeForm = ({ onSubmit, resendCode }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [verificaitonCode, setVerificationCode] = useState([]);
  
  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);

  return (
    <>
      <Nav navBarTheme={navTheme}
        showBackButton
        navBarLabel="Rooms"
        prevDestination="/rooms" />
      <PageContent
        background="dark">
        <Wrapper>
          <Header>
            <Typography
              fontStyle="primaryH3"
              color="secondaryLight"
              align="left"
            >
              Enter the code we sent to you
            </Typography>
          </Header>

          <VerificationCodeInput>
            <ReactCodeInput
              className="SmsCode"
              type="number"
              fields={4}
              onChange={(value) => setVerificationCode(value)}
              onComplete={onSubmit}
            />
          </VerificationCodeInput>

          <Info>
            <Typography
              fontStyle="secondaryBody2"
              color="dark10"
              align="left"
              style={{ marginBottom: '16px' }}
            >
              Please enter the four-digit verification code sent to you.
            </Typography>
            <Typography
              fontStyle="secondarySubtitle3"
              color="white"
              align="left"
              style={{ cursor: 'pointer' }}
              onClick={resendCode}
            >
              Resend Code
            </Typography>
          </Info>
        </Wrapper>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

ConfirmationCodeForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  resendCode: PropTypes.func.isRequired,
};

export default ConfirmationCodeForm;