const paymentMethods = {
  applePay: {
    label: 'Apple Pay',
    white: <AppleIconWhite />,
    dark: <AppleIconDark />,
  },
  mastercard: {
    label: 'Mastercard',
    white: <AppleIconWhite />,
    dark: <AppleIconDark />,
  },
  visa: {
    label: 'Visa',
    white: <AppleIconWhite />,
    dark: <AppleIconDark />,
  },
};
const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
`;
const PaymentMethod = ({ paymentMethod, variant }) => {
  return(
    <Wrapper>
      {paymentMethods[paymentMethod][variant]}
      <Typography
        fontStyle="secondarySubtitle4"
        align="left"
        color="dark50"
        style={{paddingLeft:'12px'}}
      >
        {paymentMethods[paymentMethod].label}
      </Typography>
    </Wrapper>);
};
PaymentMethod.propTypes = {
  paymentMethod: PropTypes.oneOf(['applePay', 'mastercard', 'visa']),
  variant: PropTypes.oneOf(['white', 'dark']),
};

export default PaymentMethod;
