import axios from "../../configs/axios";

export default class AuthApi {
  verificationUrl = "/v1/users/verifications";
  loginUrl = "/v1/users/login";
  updateUrl = "/v1/users";
  
  async getCode(data) {
    return axios.post(this.verificationUrl, data).then(res => res.data);
  }

  async login(data) {
    return axios.post(this.loginUrl, data).then(res => res.data);
  }

  async updateUser(id, data) {
    return axios.put(`${this.updateUrl}/${id}`, data).then(res => res.data);
  }
}