import {
  GET_CODE,
  LOGIN,
  UPDATE_USER,
  RESTORE_TOKEN,
  LOGOUT,
} from './actions';
import { success, error } from '../../utils/store';

const initialState = {
  data: {
    user: null,
    token: "",
  },
  loading: false,
  error: null,
};

export default (state = initialState, action) => {
  const { payload } = action;

  switch (action.type) {
    case UPDATE_USER:
    case LOGIN:
    case GET_CODE: {
      return {
        ...state,
        loading: true,
        error: null,
      };
    }
    case LOGOUT: {
      return {
        ...initialState
      };
    }
    case RESTORE_TOKEN: {
      return {
        ...state,
        data: {
          ...state.data,
          token: payload,
        },
      };
    }
    case success(GET_CODE): {
      return {
        ...state,
        loading: false,
      };
    }
    case success(LOGIN): {
      const { token, user } = payload;

      return {
        ...state,
        data: {
          ...state.data,
          user,
          token,
        },
        loading: false,
      };
    }
    case success(UPDATE_USER): {
      return {
        ...state,
        data: {
          ...state.data,
          user: payload, 
        },
        loading: false,
      };
    }
    case error(UPDATE_USER):
    case error(LOGIN):
    case error(GET_CODE): {
      return {
        ...state,
        loading: false,
        error: payload,
      };
    }
    default:
      return state;
  }
};
