export class Card {
  firstName = "";
  lastName = "";
  number = "";
  verificationValue = "";
  month = "";
  year = "";
  type = "";

  constructor(fname, lname, number, cvv, month, year, type) {
    this.firstName = fname;
    this.lastName = lname;
    this.number = number;
    this.verificationValue = cvv;
    this.month = month;
    this.year = year;
    this.type = type;
  }

  toJSON() {
    return {
      first_name: this.firstName,
      last_name: this.lastName,
      number: this.number,
      verification_value: this.verificationValue,
      month: this.month,
      year: this.year,
    };
  }
}

export class AppCard {
  token = "";
  last_4 = "";
  cardType = "";
  expYear = "";
  expMonth = "";

  constructor(token, card) {
    this.token = token;
    this.last_4 = card.number.slice(-4);
    this.cardType = card.type;
    this.expYear = card.year;
    this.expMonth = card.month;
  }

  toJSON() {
    return {
      token: this.token,
      last_4: this.last_4,
      card_type: this.cardType,
      exp_year: this.expYear,
      exp_month: this.expMonth,
    };
  }
}