import React from 'react';
import InputGhost from '../UI/InputGhost';
import RoundIconButton from '../UI/Buttons/RoundIconButton';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const FormWrap = styled.form`
  display: flex;
  justify-content: space-between;
  padding-right: 40px;
  flex-flow: wrap;
  @media (max-width: 768px) {
      padding-right: 28px;
  }
`;
// eslint-disable-next-line react/display-name
const PhoneInputForm = React.forwardRef(({ submitForm,phone, changePhone,showSubmit  }, ref) => {
  
  return (
    <FormWrap
      onSubmit={submitForm}>
      <InputGhost
        type='tel'
        maxLength="12"
        onChange={changePhone}
        value={phone}
        ref={ref} />
      {showSubmit && <RoundIconButton/>}
    </FormWrap>
  );
});

export default PhoneInputForm;

PhoneInputForm.propTypes = {
  submitForm:PropTypes.func,
  phone:PropTypes.string,
  changePhone:PropTypes.func,
  showSubmit:PropTypes.bool
};
