import React from 'react';
import styled from 'styled-components';
import CheckBoxIconEnable from '../../../assets/icons/CheckBoxIcon_enable';
import CheckBoxIconSelected from '../../../assets/icons/CheckBoxIcon_selected';
import PropTypes from 'prop-types';

const Wrapper = styled.label`
  display: flex;
  width: 100%;
  align-items: flex-start;
  padding-left: 0px;
  padding-top: 16px;
  padding-bottom: 16px;
  &:hover {
    cursor: pointer;
    background-color: ${(props) =>
    props.fill === 'white' ? props.theme.colors.primaryDarkest : '#ffffff'};
  }
  @media (max-width: 768px) {
    //padding-left: 28px;
  }
`;

const Checkbox = ({ children, label, handleChange, selected, fill,...props }) => {
  return (
    <Wrapper
      htmlFor={label}
      fill={fill}
      {...props}
    >
      <input
        type="checkbox"
        id={label}
        hidden
        onChange={handleChange} />
      {selected ? (
        <CheckBoxIconSelected
          width="24px"
          height="24px"
          fill={fill || 'black'} />
      ) : (
        /////////or////////
        <CheckBoxIconEnable
          width="24px"
          height="24px"
          fill={fill || 'black'} />
      )}
      {children}
    </Wrapper>
  );
};
Checkbox.propTypes = {
  children: PropTypes.node || PropTypes.string,
  label: PropTypes.string,
  handleChange: PropTypes.func,
  selected: PropTypes.bool,
  fill: PropTypes.string,
};
export default Checkbox;
