import React, { useEffect, useRef, useState } from 'react';
import Nav from '../../components/Layout/Nav';
import AppFooter from '../../components/Layout/AppFooter';
import { StyledPageContent } from '../../globalStyles';
import PrimaryButton from '../../components/UI/Buttons/PrimaryButton';
import Typography from '../../components/UI/Typography';
import { AccountInput, UploadButton, UserPic, Wrapper, WrapperForm } from './styles';
const dummyData = {
  id: '123',
  full_name: 'Anthony King',
  phone: '+1111111111',
  email: 'aking@gmial.com',
  image_url:
    'https://image.shutterstock.com/image-photo/portrait-happy-businessman-arms-crossed-260nw-388786822.jpg',
};
const Account = (props) => {
  const [navTheme, setNavTheme] = useState('white');
  const [picture, setPicture] = useState({ file: '', imagePreviewUrl: dummyData.image_url || '' });
  const [userName, setUserName] = useState(dummyData.full_name || '');
  const [email, setEmail] = useState(dummyData.email || '');
  const [phone, setPhone] = useState(dummyData.phone || '');
  const inputRef = useRef();

  useEffect(() => {
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('black');
  }, []);

  const handleUploadImage = (e) => {
    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
      setPicture({
        file: file,
        imagePreviewUrl: reader.result,
      });
    };
    reader.readAsDataURL(file);
  };
  
  const handleSaveAccount = (e) => {
    e.preventDefault();
    const response = {
      id: dummyData.id,
      full_name: userName,
      phone: phone,
      email: email,
      image_url:picture.imagePreviewUrl
    };
    console.log(response);
  };
  return (
    <>
      <Nav
        showBackButton={true}
        navBarTheme={navTheme}
        navBarLabel="Profile"
      />
      <StyledPageContent>
        <UserPic userImage={picture.imagePreviewUrl}>
          <input
            ref={inputRef}
            hidden
            onChange={(e) => {
              handleUploadImage(e);
            }}
            type="file"
          />
          <UploadButton
            onClick={() => {
              inputRef.current.click();
            }}
          >
            CHANGE PHOTO
          </UploadButton>
        </UserPic>
        <WrapperForm onSubmit={(e)=>handleSaveAccount(e)}>
          <div>
            <AccountInput
              placeholder="Full Name"
              change={(e) => {
                setUserName(e.target.value);
              }}
              value={userName}
            />
            <AccountInput
              placeholder="E-mail"
              change={(e) => {
                setEmail(e.target.value);
              }}
              type='email'
              value={email}
            />
            <AccountInput
              placeholder="Phone Number"
              change={(e) => {
                setPhone(e.target.value);
              }}
              value={phone}
            />
          </div>
          <PrimaryButton type='submit' disabled={!userName || !phone || !email} >
            <Typography fontStyle="secondarySubtitle1" align="center" color="white">
            Save
            </Typography>
          </PrimaryButton>
        </WrapperForm>
      </StyledPageContent>
      <AppFooter footerTheme={navTheme} />
    </>
  );
};

export default Account;
