import axios from "../../configs/axios";

export default class WalletApi {
  cardsUrl = "/v1/users/payment_cards";
  
  async getCards() {
    return axios.get(this.cardsUrl).then(res => res.data);
  }

  async createCard(card) {
    return axios.post(this.cardsUrl, card).then(res => res.data);
  }
}