import { success, error } from '../../utils/store';
import WalletApi from '../../services/wallet/api';
import SpreedlyApi from '../../services/spreedly/api';
import { getErrorMessage } from '../../utils/api';
import appConf from '../../configs/app';
import { AppCard } from '../../entities/payments';

export const GET_CARDS = 'GET_CARDS';

const getCardsRequest = () => {
  return {
    type: GET_CARDS,
  };
};

const getCardsSuccess = (payload) => {
  return {
    type: success(GET_CARDS),
    payload,
  };
};

const getCardsError = (msg) => {
  return {
    type: error(GET_CARDS),
    payload: msg,
  };
};

export const getCards = () => async (dispatch) => {
  try {
    dispatch(getCardsRequest());
    const data = await new WalletApi().getCards();
    dispatch(getCardsSuccess(data));
  } catch (e) {
    dispatch(getCardsError(getErrorMessage(e)));
  }
};

export const CREATE_CARD = 'CREATE_CARD';

const createCardRequest = () => {
  return {
    type: CREATE_CARD,
  };
};

const createCardSuccess = (payload) => {
  return {
    type: success(CREATE_CARD),
    payload,
  };
};

const createCardError = (msg) => {
  return {
    type: error(CREATE_CARD),
    payload: msg,
  };
};

export const createCard = (card) => async (dispatch) => {
  try {
    dispatch(createCardRequest());
    const token = await new SpreedlyApi(appConf.spreedlyEnv).tokenize(card);
    const created = await new WalletApi().createCard(new AppCard(token, card));
    dispatch(createCardSuccess(created));
  } catch (e) {
    dispatch(createCardError(getErrorMessage(e)));
  }
};
