import { success, error } from '../../utils/store';
// import { error as errorToast } from "../../utils/toasts";
import { getErrorMessage } from '../../utils/api';

export const INIT_FILTER_DATES = 'INIT_FILTER_DATES';

export const initFilterDates = (start, end) => ({
  type: INIT_FILTER_DATES,
  payload: {
    start,
    end,
  },
});

export const UPDATE_FILTER = 'UPDATE_FILTER';

export const updateFilter = ({ dateRange, priceRange, promocodes, guests,currency }) => {
  const data = {
    dateRange,
    priceRange,
    promocodes,
    guests,
    currency
  };
  return {
    type: UPDATE_FILTER,
    payload: data,
  };
};