import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import Typography from '../../../../components/UI/Typography';
import Nav from '../../../../components/Layout/Nav';
import AppFooter from '../../../../components/Layout/AppFooter';
import PhoneInputForm from '../../../../components/PhoneInputForm';
import { Header, Info, NumberInput, Wrapper } from './styles';
import { PageContent } from '../../../../globalStyles';
import { type as contactType } from '../constants';

const PhoneFormForCode = ({ to, onSubmit, auth: { loading } }) => {
  const [navTheme, setNavTheme] = useState('white');
  const [phone, setPhone] = useState('+1');
  const inputRef = useRef();
  
  const handleSubmitPhoneNumber = (e) => {
    e.preventDefault();
    onSubmit({ phone });
  };
  
  const handleChangePhoneNumber = (e) => {
    setPhone(e.target.value);
  };
  
  useEffect(() => {
    inputRef.current?.focus();
    window.innerWidth > 768 ? setNavTheme('white') : setNavTheme('white');
  }, []);
  
  return (
    <>
      <Nav navBarTheme={navTheme}
        showBackButton
        navBarLabel="Rooms"
        prevDestination="/rooms" />
      <PageContent
        background="dark">
        <Wrapper>
          <Header>
            <Typography
              fontStyle="primaryH3"
              color="secondaryLight"
              align="left"
            >
              Enter your mobile number
            </Typography>
          </Header>
          <NumberInput>
            <PhoneInputForm
              ref={inputRef}
              submitForm={handleSubmitPhoneNumber}
              phone={phone}
              changePhone={handleChangePhoneNumber}
              showSubmit={phone.length === 12 && !loading}
            />
          </NumberInput>

          <Info>
            <Typography
              fontStyle="secondaryBody2"
              color="dark10"
              align="left"
              style={{ marginBottom: '16px' }}
            >
              We&apos;ll send you a verification code via text. Standard message and data rates
              apply.
            </Typography>
            <Typography
              fontStyle="secondarySubtitle3"
              color="white"
              align="left"
              style={{ cursor: 'pointer' }}
              onClick={() => to(contactType.email)}
            >
              Or continue with email instead.
            </Typography>
          </Info>
        </Wrapper>
      </PageContent>
      <AppFooter
        footerTheme={navTheme} />
    </>
  );
};

PhoneFormForCode.propTypes = {
  to: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
};

export default PhoneFormForCode;